.map{
    height: 500px;
    background-color: white;
    padding: 1rem;
    border-radius: 20px;
    margin-top: 20px;
    margin-bottom: 20px;
    box-shadow: 0 0 10px -5px rgba(0,0,0,0.5);
}
.map .leaflet-container{
    height: 100%;
}
.info_flag img{
    width: 100px;
    border-radius: 5px;
}
.info-name{
    font-size: 18px;
    font-weight: bold;
    color: #555;
}
.info_flag{
    height: 80px;
    width: 100%;
    background-size: cover;
    border-radius: 8px;
    background-position: center;
}